var maxNum = 9, _self;
export default {
	data() {
		return {
			list: [1,2,3,4,5,6],
			imgLists : [],
            btnImg : true,
            graceSelectTips: [
				{ name: 'php', value: '0', checked: true },
				{ name: '.net', value: '1', checked: false },
				{ name: 'java', value: '2', checked: false },
				{ name: 'nodeJs', value: '3', checked: false },
				{ name: '其他', value: '4', checked: false },
				{ name: '其他', value: '5', checked: false },
				{ name: '其他', value: '6', checked: false },
			],
			textValue:'',
			goodsList:[],
			goddsName: [],
			pickValue:[]
		}
	},
	onLoad:function(){
		_self = this;
	},
	onShow(){
		this.getgoodsList()
		
	},
	watch: {
		pickValue(){
			/**
			 * 获取商品标签标签
			 */
			this.getfindtag(this.pickValue.cate_id)
		}
	},
	methods:{
		getfindtag(data){
			this.$http.getfindtag({gid:data}).then(res=>{
				if(res.status == 200) {
					this.graceSelectTips = res.data.data
				}
			})
		},
		
		addImg : function(){
			var num = maxNum - _self.imgLists.length;
			if(num < 1){return false;}
			
			uni.chooseImage({
				count: num,
				sizeType: ['compressed'],
				success: function(res) {
					_self.imgLists = _self.imgLists.concat(res.tempFilePaths);
					if (_self.imgLists.length >= maxNum){
						_self.btnImg = false;
					}
					uni.hideLoading();
					
				}
			});
		},
		removeImg : function(e){
			var index = e.currentTarget.id.replace('grace-items-img-', '');
			_self.imgLists.splice(index, 1);
			_self.imgLists = _self.imgLists;
			if (_self.imgLists.length < maxNum) {
				_self.btnImg = true;
			}
		},
		showImgs : function(e){
			var currentImg = e.currentTarget.dataset.imgurl;
			uni.previewImage({
			  urls: this.imgLists,
			  current : currentImg
			})
        },
        

        graceSelectChange: function (e) {
			var checkVal = e.detail.value;
			for (var i = 0; i < this.graceSelectTips.length; i++) {
				if (checkVal.indexOf(this.graceSelectTips[i].value + '') != -1) {
					this.graceSelectTips[i].checked = true;
				} else {
					this.graceSelectTips[i].checked = false;
				}
			}
			this.graceSelectTips = this.graceSelectTips;
		},

		/**
		 * 图片上传
		 */
		getuploadspic () {
			if(this.imgLists.length == 0) {
				this.$toast('请先上传图片')
				return false
			}
			return new Promise(resolve=>{
				this.$http.getuploadspic(this.imgLists,'apicloud/find/uploadspic').then(res=>{
					if(res.status == 200) {
						resolve(res.images)
					}
				})
			})
		},
		/**
		 *提交审核
		 */
		async getaddthorder(){
			if(this.textValue == '') {
				this.$toast('请输入分享内容')
				return false
			}
			// if(this.pickValue.length == '') {
			// 	this.$toast('请输选择商品')
			// 	return false
			// }
			let str = await this.getuploadspic()
			let data = {
				describe:this.textValue,
				tags:'',
				gid:this.pickValue.id,
				pic: str.join(',')
			}
			this.$loading()
			this.$http.getaddfind(data).then(res=>{
				if(res.status == 200) {
					this.$toast(res.mess)
					setTimeout(()=>{
						this.getback()
					},1000)
				}
			})
		},



		getgoodsList(){
            this.$http.getgoodsList().then(res=>{
                if(res.status == 200) {
                    this.goodsList = res.data
                }
            })
		},
		bindPickerChange(e) {
			this.goodsList.forEach( (element,index) => {
				if(e.detail.value == index) {
					this.pickValue = element
				}
			});
		}
	}
}
